import topSearch from "@/components/topSearch.vue";
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let pageSize = 20
let _this,timer
const JsyServer = require("@/services/jsy-server.js");
export default {
  data() {
    return {
	  loadingType: 'more',
      tabOne: 0,
      //是否全部选中
      list: [{
        option: false
      }, {
        option: false
      }, {
        option: false
      }],
      inputValue: '',
      //跟进记录搜索内容
      inputValueOne: '',
      
      timeIconStatus: false,
      //时间区间选择是否被打开
      
      setOver: false,
      //返回该页是否刷新
      selectContent: [{
        name: '全部',
        id: 0
      }, {
        name: '买家',
        id: 2
      }, {
        name: '卖家',
        id: 4
      }],
      bindSelect: false,
      //是否点开搜素类别
      loading: false,
     //默认身份
	  pupDef:'',
	  //客户列表
	  customerList: [],
	  pageNum: 1,
	  isDoRefresh:false,
	  totalPage: ''
    };
  },

  onReachBottom: function () {
	 if (timer != null) {
	            clearTimeout(timer);
	        }
	   timer = setTimeout(function() {
	        _this.getMoreCustomer('',_this.pageNum,pageSize);
	    }, 1000);

	  
	 

  },
  onPullDownRefresh: function () {
	
	_this.getCustomerList('',_this.pageNum,pageSize);

  },
  onShow: function () {
    let pages = getCurrentPages();
    let currPage = pages[pages.length-1];
    if (currPage.data.isDoRefresh == true){
		   currPage.data.isDoRefresh = false;
		   _this.getCustomerList('',_this.pageNum,pageSize);
    	 }
  },
  onLoad: function (options) {
    _this = this;
    //let userInfo = wx.getStorageSync("userInfo");
	if (this.checkLogin()){
	    this.pupDefault()
	    //获取职位列表
		_this.getCustomerList('',_this.pageNum,pageSize);
	
	}
  }
     
  ,
  components: {
	  topSearch,
	  uniLoadMore
  },
  props: {},
  methods: {
	getMoreCustomer:function(keyword,pageNum,pageSize){
		    if (_this.loadingType !== 'more') {//loadingType!='more';直接返回
		    	return false;
		    }
			_this.loadingType = 'loading';
			uni.showNavigationBarLoading();//显示加载动画
			
		    let _postCode = uni.getStorageSync('pupDefault')
		    let _data= {
				keyword: keyword,
				pageNum: pageNum,
				pageSize: pageSize,
				postCode: _postCode
			}
		   JsyServer.bsList(_data).then(res => {
			  if (res.data.data.list.length == 0) {//没有数据
			      console.log("no data")
			  	_this.loadingType = '';
			  	uni.hideNavigationBarLoading();//关闭加载动画
			  	return;
			 }
			  _this.pageNum++;//每触底一次 page +1
		     console.log(res)
		     _this.customerList = _this.customerList.concat(res.data.data.list)
			_this.loadingType = 'more';//将loadingType归0重置
			uni.hideNavigationBarLoading();//关闭加载动画
			
		   }).catch(err => {
			  
		     console.log("getBSList=err==", err);
		   });
		  
	},
	  
    blurInput: function (e) {
      console.log(e.detail.value);
      this.inputValueOne = e.detail.value
    },
    bindSearch: function (e) {
      recordPage = 1;
      this.setData({
        inputValue: e.detail.value
      });
      this.getRecordList();
    },
    // 点开搜索下拉选项
    bindSelectFunc: function () {
      this.setData({
         bindSelect: !this.bindSelect
      });
    },
    // 点击选项
    bindSelectContent: function (e) {
      let index = e.currentTarget.dataset.index;
      let selectContent = this.selectContent;
      let obj = selectContent[0];
      selectContent[0] = selectContent[index];
      selectContent[index] = obj;
     
      this.selectContent = selectContent
   

      if (index > 0) {
        this.tapSearch();
      }
    },
    // 点击搜索
    tapSearch: function (value) {
	  uni.showLoading({
	    title: '搜索中...'
	  });
      this.getCustomerList(value,1,pageSize);
	  setTimeout(function() {
	  		  uni.hideLoading();
	  }, 2000);
	  
    },
    // 获取客户列表
    getCustomerList: function (keyword,pageNum,pageSize) {
		_this.pageNum = 1
		_this.loadingType = 'more';
		uni.showNavigationBarLoading();
		
		let _postCode = uni.getStorageSync('pupDefault')
	    let _data= {
			keyword: keyword,
			pageNum: _this.pageNum,
			pageSize: pageSize,
			postCode: _postCode
		}
       JsyServer.bsList(_data).then(res => {
		   _this.pageNum++;
         console.log(res)
         _this.customerList = res.data.data.list
		 _this.totalPage = res.data.data.totalPage
       }).catch(err => {
         console.log("getBSList=err==", res);
       });
	   uni.hideNavigationBarLoading();
	   uni.stopPullDownRefresh();//得到数据后停止下拉刷新
    },
	pupDefault:function(){
		JsyServer.pupDefault().then(res => {
			console.log(res)
			_this.pupDef = res.data.data.msg
		}).catch(err => {
		  console.log("pupDefault=err==", res);
		});
	},
    setNavButton:function(val){
		let pages = getCurrentPages();
		let page = pages[pages.length-1];
		let currentWebview = page.$getAppWebview();
		let titleObj = currentWebview.getStyle().titleNView;  
		                    
		titleObj.buttons[0].text = val;  
		currentWebview.setStyle({  
			titleNView: titleObj  
		});  
		
	},
    tapTabTwo: function (e) {
      let index = e.currentTarget.dataset.index;

      this.tabTwo = index;
	  console.log(index);
      this.getCustomerList();
	  
    },
    
   

       
    //设置当前时间
    setTime: function () {
      let newDate = new Date();
      let month = newDate.getMonth() + 1;
      month = month < 10 ? "0" + month : month;
      let newTime = newDate.getFullYear() + '-' + month + '-' + newDate.getDate();
      this.setData({
        newTime: newTime
      });
    },
   
 
    
    //跳转到新建客户页面
    goCustomerCreated: function () {
		  wx.navigateTo({
		    url: '/pages/qing-f-c/sellDupty/customer-created'
		  })
    },
    //跳转到新增跟进记录页
   
    // 跳转到客户详情页
    toClientDetail: function (e) {
      // if (this.compileing) {
      //   this.tapPitchOn(e.currentTarget.dataset.index);
      // } else {
        let id = e.currentTarget.dataset.id;

		uni.navigateTo({
		  url: '/pages/qing-f-c/sellDupty/customer-details?companyCode=' + id
		});
    
    },
    
    // 重置时间
    bindReset: function () {
      this.setData({
        startDate: "",
        endDate: "",
        inputValue: ''
      });
    
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
